    transform: translateY(-50px);
  }
}

.task-item {
  flex-direction: column;
  display: flex;
}

.task-meta:hover {
  background-color: #e0e0e0;
}

.task-meta {
  align-items: center;
  display: flex;
}

.task-title {
  flex-grow: 1;
  padding-left: 10px;
}

.task-children {
  border-left: 1px dotted gray;
