body {
    min-height: 100vh;
    min-height: -webkit-fill-available;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    color: #202020;
    background-color: #ffffff;
    max-height: -webkit-fill-available;
}

html {
    height: -webkit-fill-available;
}

@keyframes task {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }
}

.task-item {
    display: flex;
    flex-direction: column;
}

.task-meta:hover {
    background-color: #e0e0e0;
}

.task-meta {
    display: flex;
    align-items: center;
}

.task-title {
    flex-grow: 1;
    padding-left: 10px;
}

.task-children {
    margin-left: 30px;
    border-left: 1px dotted grey;
}

.task-indicator {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: red;
}

.task-indicator-on {
    background-color: green;
}

.task-meta-closed {
    border-left: 4px solid grey;
    background-color: #f0f0f0;
}

.task-delete-button {
    font-size: 24px;
    width: 40px;
    height: 40px;
    color: #606060;
    font-family: "Material Icons";
    display: flex;
    justify-content: center;
    align-items: center;
}
